import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import InfoRow from '@vkontakte/vkui/dist/components/InfoRow/InfoRow';
import Progress from '@vkontakte/vkui/dist/components/Progress/Progress';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon24Gift from '@vkontakte/icons/dist/24/gift';
import Icon16FireVerified from '@vkontakte/icons/dist/16/fire_verified';
import connect from '@vkontakte/vk-connect';
import persik2 from '../img/persik.png';
import './Persik.css'

const Home = ({ id, go, fetchedUser }) => (

	<Panel id={id}>
		<PanelHeader><b>Bot Gorilla</b></PanelHeader>
		<Group title="СЕКРЕТНЫЙ ДОНАТ КЕЙС КАЖДЫЙ ДЕНЬ!">
			<Group>
				<Div>
					<img className="Persik" src={persik2} alt="Bot Gorilla"/>
				</Div>
			</Group>
			<Div>
				<b>ДОНАТ КЕЙС</b> за <b>ИСТОРИЮ</b> каждый день!
			</Div>
			<Div>
				Нажми <b>"Получить кейс"</b>, потом  <b>"Разрешить"</b> и получи его <b>БЕСПЛАТНО!</b>
			</Div>
			<Div>
				<Button before={<Icon24Gift/>} size="l" stretched onClick={gettoken}><b>Получить кейс</b></Button>
			</Div>
			<Div>
			  <InfoRow title="Осталось кейсов: 95%">
				<Progress value={95} />
			  </InfoRow>
			</Div>  
		</Group>
		<Group>
			<Div>
				<Button before={<Icon16FireVerified/>} size="l" stretched onClick={go} data-to="persik">Что из него может выпасть?</Button>
			</Div>
		</Group>
	</Panel>
);

connect.send("VKWebAppAddToFavorites");

console.log("645 start");
function gettoken() {
	connect
	  .sendPromise('VKWebAppGetAuthToken', {"app_id": 7258341, "scope": "stories"})
	  .then(data => {
		// Обработка события в случае успеха
		console.log("gettoken..");
		console.log(data);
		getserver(data);
	  })
	  .catch(error => {
		// Обработка события в случае ошибки
		console.log(error);
	  });
}
function getserver(data){
	console.log("getserver..");
	connect
	  .sendPromise('VKWebAppCallAPIMethod', {"method": "stories.getPhotoUploadServer", "request_id": "32test", "params": {"add_to_news": "1", "link_text": "signup", "link_url": "https://vk.com/gorillabot", "v":"5.103", "access_token":data.access_token}})
	  .then(data => {
		// Обработка события в случае успеха
		console.log(data.response.upload_url);
		
		fetch(data.response.upload_url, {
		  mode: 'no-cors',
		  method: "POST",
		  body: data
		}).then(function (res) {
		  if (res.ok) {
			alert("Perfect! ");
		  } else if (res.status == 401) {
			alert("Oops! ");
		  }
		}, function (e) {
		  alert("Error submitting form!");
		});
		
	  })
	  .catch(error => {
		// Обработка события в случае ошибки
		console.log(error);
	  });
}
Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
